@import url(https://fonts.googleapis.com/css?family=Montserrat);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	background-color: #f4f4f4;
	font-family: 'Montserrat', sans-serif;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	height: 100vh;
	overflow: hidden;
	padding: 10px;
}

.testimonial-container {
	background-color: #476ce4;
	color: #fff;
	border-radius: 15px;
	margin: 20px auto;
	padding: 50px 80px;
	max-width: 768px;
	position: relative;
}

.fa-quote {
	color: rgba(255, 255, 255, 0.3);
	font-size: 28px;
	position: absolute;
	top: 70px;
}

.fa-quote-right {
	left: 40px;
}

.fa-quote-left {
	right: 40px;
}

.testimonial {
	line-height: 28px;
	text-align: justify;
	margin-top: 5%;
}

.user {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	margin-top: 5%;
}

.user .user-image {
	border-radius: 50%;
	height: 75px;
	width: 75px;
	object-fit: cover;
}

.user .user-details {
	margin-left: 10px;
}

.user .role {
	font-weight: normal;
	margin: 10px 0;
}

.progress-bar {
	background-color: #fff;
	height: 4px;
	width: 100%;
	-webkit-animation: grow 10s linear infinite;
	        animation: grow 10s linear infinite;
	-webkit-transform-origin: left;
	        transform-origin: left;
}

@-webkit-keyframes grow {
	0% {
		-webkit-transform: scaleX(0);
		        transform: scaleX(0);
	}
}

@keyframes grow {
	0% {
		-webkit-transform: scaleX(0);
		        transform: scaleX(0);
	}
}

@media (max-width: 768px) {
	.testimonial-container {
		padding: 20px 30px;
	}

	.fa-quote {
		display: none;
	}
}

